import React from 'react'
import Footer from '../components/Footer'
import NavBar from '../components/NavBar'
import '../App.css'

export default function Services() {
    return (
        <>
            <NavBar />
            <main style={{ fontFamily: 'Almarai, sans-serif', textAlign: 'right', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <h1 style={{ marginBottom: 40 }}>: </h1>
                <div style={{
                    backgroundColor: 'white',
                    height: 'auto',
                    width: '90%',
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    alignItems: 'stretch',



                    gap: 30,
                }}>
                    <Card centerTitle=' '
                        title={'Logo'}
                        description='.     '
                        photo='logo.png'
                    >
                        <div style={{}}>
                            <h5>:  </h5>
                            <p style={{ textAlign: 'center' }}>
                                 .AI <br />
                                 .JPG <br />
                                 .PNG <br />
                                 .PDF <br />
                                 .PSD <br />
                            </p>
                        </div>
                    </Card>
                    <Card centerTitle='  / '
                        title={'Social media Ad'}
                        description='     '
                        photo='socialMediaAd.png'
                    >


                        <div style={{}}>
                            <h5>:  </h5>
                            <p style={{ textAlign: 'center' }}>
                                 .AI / .PSD <br />
                                 .JPG <br />
                                 .PNG <br />
                                 .PDF <br />
                            </p>
                        </div>
                    </Card>
                    <Card
                        title={'Brand identity'}
                        centerTitle='  '
                        description='      '
                        photo='brandIdentity.png'
                    >
                        <div style={{ textAlign: 'center' }}>
                            <h5>:  </h5>
                            <p style={{ lineHeight: 1.5, fontSize: 14 }}>
                                Logo <br />
                                    <br />
                                  <br />
                                social media post template <br />
                                    <br />

                            </p>
                        </div>
                    </Card>
                    <Card centerTitle={'  '}
                        description={'     '}
                        title={'Bisuness card'}
                        finalFiles={' '}
                        photo={'bisunessCard.png'}
                    >
                        <p>
                             .AI / .PSD <br />
                             .JPG <br />
                             .PNG <br />
                             .PDF <br />
                        </p>
                    </Card>
                    <Card
                        centerTitle={' '}
                        description={'   '}
                        title={'Ad banner'}
                        finalFiles=' :  '
                        photo='adBanner.png'
                    >
                        <p>
                             .AI / .PSD <br />
                             .JPG <br />
                             .PNG <br />
                             .PDF <br />
                        </p>
                    </Card>
                    <Card
                        centerTitle='  '
                        description='       '
                        title=' '
                        finalFiles=' :  '
                        photo='customDesign.png'
                    >
                        <h4></h4>
                    </Card>

                </div>
            </main>
            <Footer />
        </>
    )
}
function Card(props) {

    function photoWidth() {
        let width = 0;
        if (props.photo === 'logo.png') {
            width = 70;
        } else width = 120;
        return width;
    }
    return (
        <div className='order-card'>
            <h3 style={{ fontSize: 15, color: 'black' || '#f8931f' }}>{props.title}</h3>
            <div style={{ overflow: 'hidden', display: 'flex', alignItems: 'center', justifyContent: 'center', borderRadius: 10, backgroundColor: 'transparent', height: 150, width: 150 }}>
                <img width={photoWidth()} src={require(`../assets/${props.photo || 'logo.png'}`)} alt='limitless graphics logo' />
            </div>
            <h3 style={{ textDecoration: 'underline' }}>{props.centerTitle || ' '}</h3>
            <p style={{ fontSize: 12 }}>
                {props.description}
            </p>
            <button className='contact-us-action-button'
                onClick={() => {
                    window.open(`https://wa.me/212682536808?text=${props.title}`.replace(' ', '%20'));
                }}
            > </button>
            <div style={{ textAlign: 'center' }}>
                <h5>{props.finalFiles}</h5>
                {props.children}
            </div>
        </div>
    )
}